import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';

import '../css/Contact.css';

const Contact = () => {
    return(
        <Container>
            <Row>
                <h1 className='connect'>I'd Love To Connect With You!</h1>
            </Row>
            <Row>
                <h5>Reach out at any of the following so we can chat</h5>
            </Row>
            <Row className='contact-row'>
                <Col md={3} className='contacti'>
                    <i className='fa fa-location-arrow fa-2x' />
                    <h3>Where I am</h3>
                    <h5>Seattle, Wa</h5>
                    <p>*I'm open to relocate!</p>
                </Col>
                <Col md={3} className='contacti'>
                    <i className='fas fa-envelope contacti fa-2x' />
                    <h3>Email me at</h3>
                    <h5><a href='mailto:dalesimmons0210@gmail.com'>dalesimmons0210@gmail.com</a></h5>
                </Col>
                <Col md={3} className='contacti'>
                    <i className='fab fa-linkedin-in contacti fa-2x' />
                    <h3>Let's Connect</h3>
                    <h5><a href='https://www.linkedin.com/in/dalejsimmons/'>Linkedin</a></h5>
                </Col>
                <Col md={3} className='contacti'>
                    <i className='fa-github fab contacti fa-2x' />
                    <h3>See more of my works</h3>
                    <h5><a href='https://github.com/Revlisc'>Github</a></h5>
                </Col>
            </Row>
            <Row className="fillpage">
                <p>.</p>
            </Row>
        </Container>
    )
}

export default Contact;